/**
 * Portfolio block editor styles
 */

@use "sass:color";
@use "../../scss/index" as *;
@use "./style.scss" as *;

// Editor-specific styles
.obx-portfolio {
  max-width: 100%  !important;
  &__item {
    position: relative;
    
    &.is-selected,
    &:hover {
      .obx-portfolio__item-actions,
      .obx-portfolio__item-image-actions {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  
  &__item-image-container {
    position: relative;
    width: 100%;
    min-height: 200px;
    background-color: $color-background-secondary;
    border-radius: $border-radius-md;
    overflow: hidden;
    margin-bottom: $spacing-md;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: $spacing-lg;
  }

  &__item-image-wrapper {
    position: relative;
    width: 100%;
    padding: 0;
    margin: 0;
    border: none;
    background: none;
    cursor: pointer;
    transition: opacity $transition-fast;
    display: block;
    height: 100%;

    &:hover {
      opacity: 0.9;
    }
  }

  &__item-image {
    max-width: 100%;
    max-height: 100%;
    width: auto;
    height: auto;
    object-fit: contain;
  }
  
  &__item-image-placeholder {
    @include flex-center;
    width: 100%;
    height: 100%;
    min-height: 200px;
    background-color: $color-background-secondary;
  }
  
  &__item-image-button {
    padding: $spacing-md $spacing-lg;
    background-color: $color-background-primary;
    border: 1px solid $color-border;
    border-radius: $border-radius-md;
    
    &:hover {
      background-color: $color-background-secondary;
    }
  }
  
  &__item-actions {
    margin-top: $spacing-md;
    opacity: 0;
    visibility: hidden;
    transition: opacity $transition-fast;
    display: flex;
    gap: $spacing-sm;
  }
  
  &__item-image-actions {
    position: absolute;
    top: $spacing-md;
    right: $spacing-md;
    display: flex;
    gap: $spacing-sm;
    opacity: 0;
    visibility: hidden;
    transition: opacity $transition-fast;
    z-index: 10;
  }

  &__edit-form {
    margin-top: $spacing-xl;
    padding: $spacing-lg;
    background-color: $color-background-primary;
    border: 1px solid $color-border;
    border-radius: $border-radius-md;
  }

  &__image-preview {
    margin: $spacing-md 0;
    
    img {
      border-radius: $border-radius-md;
      border: 1px solid $color-border;
    }
  }
  
  &__form-actions {
    margin-top: $spacing-lg;
    display: flex;
    gap: $spacing-md;
  }
  
  &__add-button {
    margin-top: $spacing-xl;
    width: 100%;
    justify-content: center;
    padding: $spacing-lg;
    border: 2px dashed $color-border;
    border-radius: $border-radius-md;
    
    &:hover {
      background-color: $color-background-secondary;
    }
  }

  // Grid layout for logo showcase
  &__items {
    display: flex;
    flex-direction: column;
    gap: $spacing-xl;
    align-items: center;
  }
} 